* {
  box-sizing: border-box;
}
html {
  width: 100%;
}
a {
  text-decoration: none;
}
header {
  z-index: 9;
  background-color: #fff;
  position: fixed;
  top: 0;
  width: 100vw;
  padding: 0 16px;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
span.logo {
  height: 64px;
  display: flex;
  align-items: center;
}
section {
  max-width: calc(100vw - 32px);
  margin: 0 auto;
  width: 592px;
}
.task-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.task-element {
  box-shadow: 0 24px 48px #e6ebf566, 0 1px 4px #e3ebfc;
  border-radius: 12px;
  padding: 16px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition-duration: 150ms;
}
.task {
  box-shadow: 0 24px 48px #e6ebf566, 0 1px 4px #e3ebfc;
  border-radius: 12px;
  background-color: #fff;
}
.task-element:hover,
.task-element:focus {
  background-color: #fafcff;
}
main {
  padding: 100px 0 36px 0;
}
@media (min-width: 768px) {
  .task-element {
    padding: 24px 32px;
  }
  .task-container {
    gap: 24px;
  }
  nav {
    max-width: 1216px;
    margin: 0 auto;
    padding: 0 64px;
    box-sizing: content-box;
  }
  header {
    padding: 0;
  }
  footer > div {
    padding: 0 64px;
    max-width: 1216px;
    margin: 0 auto;
    box-sizing: content-box;
  }
  main {
    padding: 120px 0 56px 0;
  }
}
