footer {
  width: 100vw;
  padding: 32px 0;
  background-color: rgb(238, 245, 249);
}
footer > div {
  width: 562px;
  max-width: calc(100vw - 32px);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 32px;

  display: "flex";
  justify-content: "space-between";
  color: "#66727f";
  font-weight: "700";
  font-size: "20px";
}
footer a {
  font-size: 18px;
  line-height: 24px;
  color: rgb(102, 114, 127);
  transition: 150ms;
  font-weight: 400;
}
footer a:hover {
  color: #0b1f33;
}
.footer-icon-container {
  display: flex;
  gap: 16px;
}
.footer-icon-container > a {
  background-color: #86909c;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 40px;
  height: 40px;
  transition: 150ms;
}
.telegram-link:hover {
  background-color: rgb(50, 180, 227);
}
.github-link:hover {
  background-color: rgb(2, 4, 10);
}
.mail-link:hover {
  background-color: rgb(25, 184, 250);
}
.footer-icon-container > div svg {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  footer {
    padding: 64px;
  }
  footer > div {
    padding: 0;
    width: auto;
    max-width: 1216px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  footer > div > div {
    flex: 0 0 1fr;
  }
}
