.task {
  box-shadow: 0 24px 48px #e6ebf566, 0 1px 4px #e3ebfc;
  border-radius: 12px;
  padding: 16px;
  background-color: #fff;
  position: relative;
  margin-top: 20px;
}
.task-button {
  background-color: #0d4cd3;
  color: #fff;
  width: 100%;
  border: 2px solid transparent;
  padding: 12px 38px;
  border-radius: 8px;
  line-height: 24px;
}
.task-button:hover,
.task-button:focus {
  background-color: #1d5deb;
}
.padding-12 {
  padding: 12px 0;
}
.back-button {
  display: flex;
  align-items: center;
  color: #0d4cd3;
  top: -48px;
  left: 0;
  position: absolute;
}
@media (min-width: 768px) {
  .task-element {
    padding: 24px 32px;
  }
  .task-button {
    align-self: center;
    width: auto;
  }
  .task {
    padding: 32px;
  }
}
.answer-button:hover {
  background-color: #e4ecfd;
}
.not-selected-answer {
  background-color: #e3ebfc;
}
.false-answer {
  background-color: rgba(238, 63, 88, 0.8);
}
.active-word {
  background-color: grey;
}
.selected-word {
  background-color: #e4ecfd;
}
