.modal {
  z-index: 10;
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
  background-color: rgba(11, 31, 51, 0.62);
}
.disable-scroll {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  overflow: hidden;
}
@supports (height: 100svh) {
  .modal {
    height: 100svh;
  }
}

.modal.active {
  display: flex;
}
.modal-content {
  position: relative;
  overflow: scroll;
  padding: 24px;
  background-color: #fff;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.modal-close {
  background-color: #0d4cd3;
  color: #fff;
  width: 100%;
  border: 2px solid transparent;
  padding: 12px 38px;
  border-radius: 8px;
  line-height: 24px;
}
.modal-close:hover,
.modal-close:focus {
  background-color: #1d5deb;
}
.error-modal-container {
  height: 100%;
}
@media (min-width: 768px) {
  .modal-content {
    width: 634px;
    height: auto;
    padding: 32px;
    border-radius: 12px;
    max-width: 80%;
    max-height: 80vh;
  }
  .modal-content::-webkit-scrollbar {
    display: none;
  }
  .modal-close {
    padding: 12px 38px;
    width: auto;
  }
  .error-modal-container {
    height: auto;
  }
}
