.agree {
  box-shadow: 0 24px 48px #e6ebf566, 0 1px 4px #e3ebfc;
  border-radius: 12px;
  padding: 16px;
  background-color: #fff;
}
.agree-button {
  background-color: #0d4cd3;
  color: #fff;
  width: 100%;
  border: 2px solid transparent;
  padding: 12px 38px;
  border-radius: 8px;
  line-height: 24px;
}
.agree-button:hover,
.agree-button:focus {
  background-color: #1d5deb;
}
@media (min-width: 768px) {
  .task-element {
    padding: 24px 32px;
  }
  .agree-button {
    align-self: center;
    width: auto;
  }
  .agree {
    padding: 32px;
  }
}
